import toQueryArgs from '../helpers/to-query-args';
import parseDateRange from '../helpers/parse-date-range';
import { showLoading, hideLoading, showError, clearError } from './shared';

const updateTrendsRequest = (state, { trendsRequest }) => ({ trendsRequest });

const doTrendsStuff = ({ trendsRequest }, { service, newTrendsRequest }) =>
  dispatch =>
    ( dispatch('updateTrendsRequest', { trendsRequest: { ...trendsRequest, ...newTrendsRequest } }) )
      .then(() => dispatch('fetchTrendsData', { service }));

const updateTrendsData = (state, { trendsData }) => ({
  trendsData: trendsData.data,
});

const fetchTrendsData = ({ trendsRequest }, { service }) => dispatch => {
  dispatch('showLoading');
  dispatch('clearError');

  return service.fetchTrendsData(toQueryArgs(trendsRequest))
    .then(trendsData => dispatch('updateTrendsData', { trendsData }))
    .catch(error => dispatch('showError', { error }))
    .then(() => dispatch('hideLoading'));
};

const updateDateRange = (state, { service, dateRange }) => dispatch => {
  let { startDate, endDate } = parseDateRange(dateRange);
  startDate = startDate.format('YYYY-MM-DD');
  endDate = endDate.format('YYYY-MM-DD');
  return dispatch('updateDateRangeString', { dateRange })
    .then(() => dispatch('doTrendsStuff', { service, newTrendsRequest: { startDate, endDate } }));
};

const updateDateRangeString = (state, { dateRange }) => ({
  dateRange,
});

const updateInterval = ({ dateRange }, { interval, selectableDateRanges, service }) => dispatch => {
  const selectedDateRange = selectableDateRanges
    .map(range => range.range)
    .includes(dateRange) ? dateRange : selectableDateRanges[0].range;

  let { startDate, endDate } = parseDateRange(selectedDateRange);

  startDate = startDate.format('YYYY-MM-DD');
  endDate = endDate.format('YYYY-MM-DD');

  return dispatch('updateDateRangeString', { service, dateRange: selectedDateRange })
    .then(() => dispatch('doTrendsStuff', { service, newTrendsRequest: { interval, startDate, endDate } }));
};

export default {
  updateTrendsRequest,
  doTrendsStuff,
  updateTrendsData,
  fetchTrendsData,
  showLoading,
  hideLoading,
  showError,
  clearError,
  updateDateRange,
  updateDateRangeString,
  updateInterval,
};



