import React from 'react';
import injectSheet from 'react-jss';
import { upperFirst } from 'lodash/fp';
import moment from 'moment';
import phptomoment from 'phptomoment';
import Cog from 'react-icons/lib/fa/cog';
import PropTypes from 'prop-types';
import convertErrorMessage from '../helpers/convert-error-message';
import parseDateRange from '../helpers/parse-date-range';
import { event } from '../helpers/ga';
import { trendsDimensions, trendsMetrics, TRENDS_DATE_RANGES } from '../helpers/constants';
import connect from '../helpers/connect';
import transformTrendsData from '../helpers/transform-trends-data';
import actions from '../actions/summary';
import '../../../node_modules/react-vis/dist/style.css';
import styles from './css/summary.css';
import Button from './button';
import BrandHeader from './banner/brand-header';
import MetricSelect from './selector/metric-select';
import ListTabs from './selector/list-tabs';
import TextDropDown from './selector/text-drop-down';
import BlockUI from './block-ui';
import Menu from './menu';
import IntervalPicker from './summary/interval-picker';
import Chart from './summary/chart';

class Summary extends React.PureComponent {
  constructor(props) {
    super(props);

    const { startDate, endDate } = parseDateRange('30-days');

    this.state = {
      trendsRequest: {
        metric: 'unique_page_views',
        dimension: 'posts',
        interval: 'weekly',
        startDate: startDate ? startDate.format('YYYY-MM-DD') : '',
        endDate: endDate ? endDate.format('YYYY-MM-DD') : '',
      },
      trendsData: [],
      dateRange: '30-days',
      isLoading: true,
      error: '',
    };

    this.dateFormat = phptomoment(props.dateFormat);
    this.dispatch = connect(() => this.state, this.setState.bind(this), actions);
  }

  componentWillMount() {
    this.dispatch('fetchTrendsData', { service: this.props.service });
  }

  render() {
    const { trendsRequest, isLoading, trendsData, error, dateRange } = this.state;
    const { service, classes, path, updatePath } = this.props;

    const { dates, transformedTrendsData } = transformTrendsData(trendsData);

    const yMax = Math.max.apply(Math, transformedTrendsData.map(trendsDatum => trendsDatum.y));

    const selectableDateRanges = interval =>
      TRENDS_DATE_RANGES
        .filter(item => item.supports.includes(interval));

    return (
      <div>
        { error &&
          <div className='error'>
            <p>{convertErrorMessage(error)}</p>
          </div>
        }
        <Menu currentItem={path} updatePath={updatePath} disabled={isLoading} />
        <BrandHeader />
        <div className={classes.div}>
          <h3 className={classes.dateHeader}>
            Performance in
            <TextDropDown
              options={selectableDateRanges(trendsRequest.interval).map(range => ({ value: range.range, label: range.label.toLowerCase() }))}
              selectedOption={dateRange}
              onChange={dateRange => this.dispatch('updateDateRange', { service, dateRange })}
              dataTestId="dateSelect"
            />
          </h3>
          <ListTabs
            tabs={Object.values(trendsDimensions)}
            onClick={dimension => this.dispatch('doTrendsStuff', { service, newTrendsRequest: { dimension: dimension.toLowerCase() } })}
            current={upperFirst(trendsRequest.dimension)}
            disabled={isLoading}
          />
          <div className={classes.filterBox}>
            <MetricSelect
              disabled={isLoading}
              selectRef={c => this.metricSelect = c}
              dataTestId="metricSelect"
              onChange={e => this.dispatch('doTrendsStuff', { service, newTrendsRequest: { metric: e.target.value } })}
              metric={trendsRequest.metric}
              metrics={trendsMetrics}
            />
            <div className={classes.filterRight}>
              <Button onClick={() => { event({ category: 'Settings', action: 'Open' }); window.open(this.props.settingsUrl, '_blank'); }}><Cog /></Button>
            </div>
          </div>

          <div className={classes.chartBox}>
            <BlockUI className={classes.blockUi} text='No results for this selection. Please change the filters in the report title.' blocking={isLoading || !trendsData.length} spinner={isLoading} color='#4d83b8'/>
            <Chart
              data={transformedTrendsData}
              yMax={yMax}
              dates={dates.map(date => moment(date).format(this.dateFormat))}
              metric={trendsMetrics[trendsRequest.metric]}
            />
            <div className={classes.intervalPicker}>
              <IntervalPicker
                currentItem={trendsRequest.interval}
                onClick={interval => this.dispatch('updateInterval', { service, interval, selectableDateRanges: selectableDateRanges(interval) })}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

Summary.propTypes = {
  service: PropTypes.object,
  classes: PropTypes.object,
  dateFormat: PropTypes.string,
  defaultDateRange: PropTypes.string,
  settingsUrl: PropTypes.string,
  dashboardUrl: PropTypes.string,
  path: PropTypes.string,
  updatePath: PropTypes.func,
};

Summary.defaultProps = {
  dateFormat: 'm/d/Y',
  timeFormat: 'g:i A',
  defaultDateRange: '1-day',
};

export default injectSheet(styles)(Summary);



